import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConfig } from '../../../app/app.config';
import 'rxjs/add/operator/map';

/**
 * Generated class for the MessarticlePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-messarticle',
  templateUrl: 'messarticle.html',
})
export class MessarticlePage {
  url = AppConfig.getUrl() + "/djySpider/ActivitiesDetail?cookie=" + localStorage.djycookie
  arr: any = [];
  id = this.navParams.data.id;
  title = this.navParams.data.title;

  constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public alertCtrl: AlertController, public loadingCtrl: LoadingController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MessarticlePage');
    this.get();
  }
  get() {
    let loading = this.loadingCtrl.create({
      content: '加载中...'
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.http.get(this.url + "&tid=" + this.id)
        .map(res => res.json())
        .subscribe(data => {
          resolve(data);
          if (data.status) {
            this.arr={
              text: data.data.text,
              img: data.data.imgs[0].url
            }
            console.log(this.arr)
          } else {
            let alert = this.alertCtrl.create({
              title: data.msg
            });
            alert.present();
          }
        }, err => {
          let alert = this.alertCtrl.create({
            title: err
          });
          alert.present();
        })
      loading.dismiss();
    })
  }
}



